import { Col, Container, Row } from "react-bootstrap"
import MainButton from './MainButton'
import { useForm } from '../util/use-form'
import { useHistory } from "react-router"
import { useEffect } from "react"

const App = () => {

    const { form, setForm, setProgress } = useForm()
    const history = useHistory()
    const makeYearsButton = () => {
        const items = []

        for (let i = 2021; i >= 1986; i--) {
            items.push(<Col md={2} key={`${i}-year`}><MainButton title={i} onClick={handleButton} /></Col>)
        }

        return items
    }

    const handleButton = (e) => {
        // console.log(e)
        setForm({
            ...form,
            year: e
        })

        setProgress(60)

        history.push('car-1-make')
    }

    useEffect(() => {
        setProgress(30)
    }, [setProgress])

    return (
        <Container className="main-body">
            <h1 className="text-center">Letâ€™s get started! <strong> Select Vehicle Year</strong></h1>
            <Row className="justify-content-md-center">
                <Col md={8}>
                    <Row >
                        {makeYearsButton()}
                    </Row>
                </Col>
            </Row>

        </Container>
    )
}

export default App