import { Col, Container, Row } from "react-bootstrap"
import MainButton from './MainButton'
import { useForm } from '../util/use-form'
import { useHistory } from "react-router"

const Maker = [
    'Buick',
    'Chevrolet',
    'Chrysler',
    'Dodge',
    'Ford',
    'Gmc',
    'Honda',
    'Hyundai',
    'Jeep',
    'Kia',
    "Nissan",
    "Toyota"
]

const App = () => {

    const { form, setForm, setProgress } = useForm()
    const history = useHistory()

    const makeMakersButton = () => {
        return Maker.map(maker => {
            return (
                <Col md={6} key={`${maker}-car`}>
                    <MainButton title={maker} onClick={handleButton}/>
                </Col>
            )
        })
    }

    const handleButton = (e) => {
        setForm({
            ...form,
            maker: e
        })

        setProgress(100)

        history.push('car-1-result')
    }

    return (
        <Container className="main-body">
            <h1 className="text-center"><strong>Select Vehicle Make</strong></h1>
            <Row className="justify-content-md-center">
                <Col md={8}>
                    <Row >
                        {makeMakersButton()}
                    </Row>
                </Col>
            </Row>

        </Container>
    )
}

export default App