import React from 'react'
import { Col, Container, Navbar, ProgressBar, Row } from 'react-bootstrap'
import { Redirect, Route, Switch } from 'react-router-dom'

import CarYear from '../../Component/CarYear'
import CarMake from '../../Component/CarMake'
import CarResult from '../../Component/CarResult'
import { useForm } from '../../util/use-form'

const App = () => {
    const {progress} = useForm()
    return (
        <>
            <Container>
                <Navbar >
                    <Navbar.Brand href="#/main" bg="light">Compare Car</Navbar.Brand>
                    <Navbar.Toggle />
                    <Navbar.Collapse className="justify-content-end">
                        <Navbar.Text>
                            855-581-3316
                    </Navbar.Text>
                    </Navbar.Collapse>
                </Navbar>
            </Container>
            <div className="main-progress-bar">
                <h2 className="text-center">Compare Car Insurance Rates Instantly</h2>
                <Container>
                    <Row>
                        <Col>
                            <ProgressBar now={progress} label={`${progress}% Complete`}/>
                        </Col>
                    </Row>
                
                </Container>
                
            </div>
            <Switch>
                <Route path="/car-1-year" component={CarYear}/>
                <Route path="/car-1-make" component={CarMake}/>
                <Route path="/car-1-result" component={CarResult}/>
                <Redirect from="/" to="/car-1-year" />
            </Switch>
        </>
    )
}

export default App