import { Col, Container, Row } from "react-bootstrap"
import MainButton from './MainButton'
import { useForm } from '../util/use-form'
import { useHistory } from "react-router"

const App = () => {

    const { form, setForm } = useForm()
    const history = useHistory()
    
    const handleButton = (e) => {
        setForm({})
        history.push('car-1-year')
    }

    return (
        <Container className="main-body">
            <h1 className="text-center"><strong>Result</strong></h1>
            <Row className="justify-content-md-center">
                <Col md={8}>
                    <h1 className="text-center">Year: {form.year}</h1>
                    <h1 className="text-center">Maker: {form.maker}</h1>

                    <MainButton title={'Again Select'} onClick={handleButton}/>
                </Col>
            </Row>

        </Container>
    )
}

export default App